<template>
    <div class="container flex-grow-1 container-p-y">
        <h4 class="fw-bold py-1 mb-2">
            <span class="text-muted fw-light">Administraci√≥n /</span> Usuarios
        </h4>
        <!-- Bordered Table -->
        <div class="card">
            <div class="card-body">                
                <TableVue 
                    :columnas="columnas" 
                    :url="urlTabla" 
                    @handleChangedLoading="handleChangedLoading"
                    @handleChangedData="handleChangedData">
                    <template v-slot:button>
                        <button class="btn btn-primary m-1 justify-items-start" 
                            :disabled="loading" @click="$refs.modalUsuarioRef.showModal();">
                            <i class="bx bx-plus" style="font-size:20px"/> Nuevo
                        </button> 
                    </template>
                    <template v-slot:tbody>
                        <tr v-for="(usuario, key) in data" :key="key">
                            <td>
                                {{usuario.name}}
                            </td>
                            <td class="text-center">
                                <span class="badge bg-primary me-1">{{usuario.email}}</span>
                            </td>
                            <td class="text-center">
                                <span class="badge bg-secondary me-1">{{usuario.oficina}}</span>
                            </td>
                            <td class="text-center">
                                <span class="badge me-1" :class="usuario.estado==='Activo'?'bg-success':'bg-label-danger'">
                                    {{usuario.estado}}
                                </span>
                            </td>
                            <td style="padding:0%;" class="text-center">
                                <AccionesTable 
                                :loading="loading" 
                                :showEditar="true" 
                                :showEstado="true" 
                                :estado="usuario.estado"/>
                            </td>                            
                        </tr>
                    </template>
                </TableVue>
                <!--/ Bordered Table -->
            </div>
        </div>
        <Modal ref="modalUsuarioRef">
            <template v-slot:cardBody>
                <FormUsuario ref="formUsuarioRef"/>
            </template>
        </Modal>
    </div>
</template>
<script>
import { defineComponent, ref } from 'vue'
import Modal from '@/components/Modal.vue'
import TableVue from '@/components/layout/Table.vue'
import AccionesTable from '@/components/layout/AccionesTable.vue'
import FormUsuario from '@/components/casos/FormCaso.vue'

const columnas = [
    {nombre:"Usuario",  class:"text-center bg-primary text-white fw-bold",          style:"", key:'usuario'},
    {nombre:"Correo",   class:"text-center bg-label-primary text-white  text-center",    style:"", key:'correo'},
    {nombre:"Oficina",  class:"text-center bg-label-primary text-white fw-bold text-center",    style:"", key:'oficina'},
    {nombre:"Estado",   class:"text-center bg-label-primary text-white fw-bold text-center",    style:"", key:'estado'},
    {nombre:"Acciones", class:"text-center bg-label-primary text-white fw-bold text-center",    style:"", key:'acciones'},
]
export default defineComponent({
    components: {
    Modal,
    TableVue,
    AccionesTable,
    FormUsuario
},
    setup() {
        const data = ref([]);
        const loading = ref(true);
      
        const handleChangedData = _data => {
            data.value = _data
        }

        const handleChangedLoading = _loading => {
            loading.value = _loading;
        }

        return {
            data,
            loading,
            columnas,
            urlTabla:'auth/index',
            fetch,
            handleChangedData,
            handleChangedLoading,
        }
    },

})
</script>
